<template>
  <div class="panel-container">
    <el-dialog
      :title="date"
      :visible.sync="localDialog"
      @close="handle_close"
      @open="fetch_data"
    >
      <!-- 添加计划 -->
      <div @keyup.enter="add_task">
        <el-input
          placeholder="添加计划"
          prefix-icon="el-icon-plus"
          v-model="new_task_title"
          class="input-new-task"
        >
        </el-input>
      </div>

      <!-- 计划列表 -->
      <div v-if="lists.length">
        <el-card
          shadow="hover"
          v-for="(item, index) in task_list_sort"
          :key="index"
          class="panel-card"
        >
          <el-row :gutter="20">
            <el-col :span="22">
              <div :class="{ task_finished: item.status }">
                {{ item.title }}
              </div>
            </el-col>
            <el-col :span="2">
              <div class="panel-tool">
                <i class="el-icon-delete" @click="delete_task(item)"></i>
              </div>
            </el-col>
          </el-row>
        </el-card>
      </div>
      <div v-else class="no-task">
        这天什么也没有
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { getTask, addTask, deleteTask } from "../../../api/task";

export default {
  props: {
    dialog: {
      type: Boolean,
      default: function() {
        return false;
      }
    },
    date: {
      type: String,
      default: function() {
        return "2020-12-12";
      }
    }
  },
  data() {
    return {
      lists: [],
      new_task_title: "",
      localDialog: this.dialog
    };
  },
  methods: {
    fetch_data() {
      getTask(this.$store.getters.token, {
        date: this.date
      }).then(response => {
        this.lists = response.data;
      });
    },
    add_task() {
      addTask(this.$store.getters.token, {
        title: this.new_task_title,
        add_time: this.date
      }).then(() => {
        this.$notify({
          title: "系统通知",
          message: "添加计划成功",
          type: "success"
        });
        this.new_task_title = "";
        this.fetch_data();
      });
    },
    handle_close() {
      this.$emit("dialogchange", this.localDialog);
    },
    delete_task(task) {
      deleteTask(this.$store.getters.token, task.task_id).then(() => {
        for (let i = 0; i < this.lists.length; i++) {
          if (this.lists[i] === task) {
            this.lists.splice(i, 1);
            this.$notify({
              title: "系统通知",
              message: "删除计划成功",
              type: "success"
            });
          }
        }
      });
    }
  },
  watch: {
    dialog(val) {
      this.localDialog = val;
    }
  },
  computed: {
    task_list_sort() {
      // eslint-disable-next-line vue/no-side-effects-in-computed-properties
      return this.lists.sort((m, n) => {
        if (m.status !== n.status) {
          return m.status - n.status;
        } else {
          return m.task_id - n.task_id;
        }
      });
    }
  }
};
</script>

<style scoped>
.panel-card {
  margin: 5px 0;
}

.panel-tool {
  display: flex;
  justify-content: flex-end;
}

.panel-tool i:hover {
  cursor: pointer;
}

.task_finished {
  text-decoration: line-through;
}

.no-task {
  text-align: center;
  font-size: x-large;
  color: #909399;
  padding-top: 30px;
}
</style>
