<template>
  <div class="container">
    <el-card shadow="always">
      <el-calendar>
        <template slot="dateCell" slot-scope="{ date, data }">
          <p :class="data.isSelected ? 'is-selected' : ''" @click="panel(date)">
            {{
              data.day
                .split("-")
                .slice(1)
                .join("-")
            }}
            {{ lists[trans_time(date)] ? "✔️" : "" }}
          </p>
        </template>
      </el-calendar>
    </el-card>

    <!-- 计划列表 -->
    <plan-panel
      :dialog="dialog"
      @dialogchange="dialogchange"
      :date="target_date"
    />
  </div>
</template>

<script>
import { getPlan } from "../../api/task";
import planPanel from "./components/planPanel";

export default {
  components: {
    planPanel
  },
  data() {
    return {
      lists: {},
      dialog: false,
      target_date: ""
    };
  },
  methods: {
    fetch_data() {
      getPlan(this.$store.getters.token).then(response => {
        this.lists = response.data;
      });
    },
    trans_time(date) {
      return this.$moment(date).format("Y-MM-DD");
    },
    dialogchange(val) {
      this.dialog = val;
    },
    panel(date) {
      this.target_date = this.trans_time(date);
      this.dialog = true;
    }
  },
  mounted() {
    this.fetch_data();
  }
};
</script>

<style scoped>
.container {
  padding: 40px 60px;
}
.is-selected {
  color: #1989fa;
}
</style>
